import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-lazada-printer',
  templateUrl: './lazada-printer.component.html',
  styleUrls: ['./lazada-printer.component.scss']
})
export class LazadaPrinterComponent implements OnInit {
  root: any;
  constructor(
    private router: Router
  ) {
    this.root = this.router.getCurrentNavigation().extras.state;
    this.root['bill']['address_store'] = this.root['info_Shop'].data?.address
    
    

  }

  ngOnInit(): void {

  }
  goBack() {
    this.router.navigate([this.root.path], { state: this.root })
  }

}
